export default {
	data() {
		return {
			user: null
		};
	},

	methods: {
		authGetUserInfo() {
			this.$toast.loading({
				duration: 0,
				mask: true,
				message: '登录中...'
			});

			const that = this;
			uni.getSetting({
				success(res) {
					const userInfo = res.authSetting['scope.userInfo'];
					if (userInfo) {
						that.getUserInfo();
					} else {
						uni.authorize({
							scope: 'scope.userInfo',
							success: res => {},
							fail: () => {
								that.$toast('授权失败，请允许授权获取个人基本信息');
							},
							complete: () => {}
						});
					}
				},
				fail: () => {
					that.$toast('授权失败，请允许授权获取个人基本信息');
				}
			});
		},

		getUserInfo() {
			uni.getUserInfo({
				success: res => {
					console.log(res);
					const userInfo = res.userInfo;
					const nickName = userInfo.nickName;
					const avatarUrl = userInfo.avatarUrl;
					const gender = userInfo.gender; //性别 0：未知、1：男、2：女

					// 发送给服务器用户信息

					const provider = 'qq';






					const data = {
						name: nickName,
						mini_program_avatar_url: avatarUrl,
						gender: gender == 1 ? 'male' : 'female',
						provider: provider
					};

					this.login(data);
				},
				fail: () => {
					this.$toast('授权获取个人基本信息失败');
				},
				complete: () => {}
			});
		},

		login(data) {
			uni.login({

				provider: 'qq',






				success: res => {
					console.log('code：' + res.code);
					data['code'] = res.code;
					const url = this.$urlConfig.host_url + this.$urlConfig.user_login_path;

					this.$http
						.postRequest(url, data)
						.then(response => {
							console.log(response.results);
							this.user = response;
							uni.setStorage({
								key: this.$storage.k_user,
								data: response
							});
							this.$toast.success('登录成功');
						})
						.catch(error => {
							this.$toast(error.errMsg);
						})
						.finally(() => {});
				},
				fail: res => {
					this.$toast('登录失败');
				},
				complete: () => {}
			});
		},

		settingClick() {
			uni.navigateTo({
				url: '/pages-mine/setting/index'
			});
		}
	},

	onLoad() {
		uni.getStorage({
			key: this.$storage.k_user,
			success: res => {
				this.user = res.data;
			}
		});
	}
};
